import React from 'react';


import {
    SidebarContainer,
    Icon,
    CloseIcon,
    SidebarMenu,
    SidebarLink,
    SidebarRoute,
    SideBtnWrap,
} from './SidebarElements'

const Sidebar = ({isOpen, toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle} >
            <Icon>
                <CloseIcon></CloseIcon>
            </Icon>
            <SidebarMenu>
                <SidebarLink  to="/"> Hamburgers </SidebarLink>
                <SidebarLink  to="/"> Steaks </SidebarLink>
            </SidebarMenu>
            <SideBtnWrap>
                <SidebarRoute  to="/">Oder now </SidebarRoute>
            </SideBtnWrap>
        </SidebarContainer>
    )
}

export default Sidebar
